import { logger, Direction, EventType } from './jsmind.common.js';

export class LayoutProvider {
    constructor(jm, options) {
        this.opts = options;
        this.jm = jm;
        this.isside = this.opts.mode == 'side';
        this.bounds = null;

        this.cache_valid = false;
    }
    init() {
        logger.debug('layout.init');
    }
    reset() {
        logger.debug('layout.reset');
        this.bounds = { n: 0, s: 0, w: 0, e: 0 };
    }
    calculate_next_child_direction(node) {
        if (this.isside) {
            return Direction.right;
        }
        var children = node.children || [];
        var children_len = children.length;
        var r = 0;
        for (var i = 0; i < children_len; i++) {
            if (children[i].direction === Direction.left) {
                r--;
            } else {
                r++;
            }
        }
        return children_len > 1 && r > 0 ? Direction.left : Direction.right;
    }
    layout() {
        logger.debug('layout.layout');
        this.layout_direction();
        this.layout_offset();
    }
    layout_direction() {
        this._layout_direction_root();
    }
    _layout_direction_root() {
        var node = this.jm.mind.root;
        var layout_data = null;
        if ('layout' in node._data) {
            layout_data = node._data.layout;
        } else {
            layout_data = {};
            node._data.layout = layout_data;
        }
        var children = node.children;
        var children_count = children.length;
        layout_data.direction = Direction.center;
        layout_data.side_index = 0;
        if (this.isside) {
            var i = children_count;
            while (i--) {
                this._layout_direction_side(children[i], Direction.down, i);
            }
        } else {
            var i = children_count;
            var subnode = null;
            while (i--) {
                subnode = children[i];
                if (subnode.direction == Direction.left) {
                    this._layout_direction_side(subnode, Direction.left, i);
                } else if (subnode.direction == Direction.right) {
                    this._layout_direction_side(subnode, Direction.right, i);
                } else if (subnode.direction == Direction.top) {
                    this._layout_direction_side(subnode, Direction.top, i);
                } else {
                    this._layout_direction_side(subnode, Direction.down, i);
                }
            }
        }
    }
    _layout_direction_side(node, direction, side_index) {
        var layout_data = null;
        if ('layout' in node._data) {
            layout_data = node._data.layout;
        } else {
            layout_data = {};
            node._data.layout = layout_data;
        }
        var children = node.children;
        var children_count = children.length;

        layout_data.direction = direction;
        layout_data.side_index = side_index;
        var i = children_count;
        while (i--) {
            this._layout_direction_side(children[i], direction, i);
        }
    }
    layout_offset() {
        var node = this.jm.mind.root;
        var layout_data = node._data.layout;
        layout_data.offset_x = 0;
        // root 定位點
        layout_data.offset_y = -300;
        layout_data.outer_height = 0;
        var children = node.children;
        var i = children.length;
        var left_nodes = [];
        var right_nodes = [];
        var down_nodes = [];
        var top_nodes = [];
        var subnode = null;
        while (i--) {
            subnode = children[i];
            if (subnode._data.layout.direction == Direction.right) {
                right_nodes.unshift(subnode);
            } else if (subnode._data.layout.direction == Direction.left) {
                left_nodes.unshift(subnode);
            } else if (subnode._data.layout.direction == Direction.down) {
                down_nodes.unshift(subnode);
            } else {
                top_nodes.unshift(subnode);
            }
        }
        layout_data.left_nodes = left_nodes;
        layout_data.right_nodes = right_nodes;
        layout_data.down_nodes = down_nodes;
        layout_data.top_nodes = top_nodes;
        layout_data.outer_height_left = this._layout_offset_subnodes(left_nodes);
        layout_data.outer_height_right = this._layout_offset_right_subnodes(right_nodes);
        layout_data.outer_height_down = this._layout_offset_subnodes(down_nodes);
        layout_data.outer_height_top = this._layout_offset_top_subnodes(top_nodes);
        this.bounds.e = node._data.view.width / 2;
        this.bounds.w = 0 - this.bounds.e;
        this.bounds.n = 0;
        this.bounds.s = Math.max(layout_data.outer_height_left, layout_data.outer_height_right);
    }

    // layout both the x and y axis
    _layout_offset_subnodes(nodes) {
        var total_height = 0;
        var nodes_count = nodes.length;
        var i = nodes_count;
        var node = null;
        var node_outer_height = 0;
        var layout_data = null;
        var base_x = 0;
        var base_y = 0;
        var pd = null; // parent._data
        while (i--) {
            node = nodes[i];
            layout_data = node._data.layout;
            if (pd == null) {
                pd = node.parent._data;
            }
    
            node_outer_height = this._layout_offset_subnodes(node.children);
            if (!node.expanded) {
                node_outer_height = 0;
                this.set_visible(node.children, false);
            }
            node_outer_height = Math.max(node._data.view.height, node_outer_height);
            if (node.children.length > 1) {
                node_outer_height += this.opts.cousin_space;
            }

            layout_data.outer_height = node_outer_height;

            // if ((node.parent.topic === "element" && node.topic === "function")) {
            //     layout_data.offset_x = base_x + (pd.view.width * 1.5);
            //     layout_data.offset_y = base_y + pd.view.height; 
            //     base_y += node_outer_height + (this.opts.vspace);
            // } else {
            // }

            if (layout_data.direction === Direction.down) {
                if (node.data["option-color"] === "red") {

                    function findRootAndLongestFunctionWidth(node) {
                        // Find root
                        let currentObj = node;
                        while (currentObj && !currentObj.isroot) {
                            currentObj = currentObj.parent || null;
                        }
                        
                        if (!currentObj || !currentObj.isroot) {
                            return { error: "No root object found" };
                        }
                    
                        // Filter function nodes and find longest width
                        let functionLongestWidth = 0;
                        const functionNodes = currentObj.children.filter(e => e.data["option-color"] === "#658ef8");
                        
                        functionNodes.forEach(e => {
                            if (e._data && e._data.view && typeof e._data.view.width === 'number') {
                                functionLongestWidth = Math.max(functionLongestWidth, e._data.view.width);
                            }
                        });
                    
                        return {
                            rootObject: currentObj,
                            functionNodes: functionNodes,
                            longestWidth: functionLongestWidth
                        };
                    }
                    
                    // Usage
                    const result = findRootAndLongestFunctionWidth(node);
                    layout_data.offset_x = base_x + result.longestWidth * 1.1;
                } else {
                    layout_data.offset_x = base_x;
                }
                layout_data.offset_y = base_y + pd.view.height * 1.5; 
                base_y += node_outer_height + this.opts.vspace;

                if (node.data["option-color"] === "#658ef8") {
                    base_y += this.opts.vspace ;
                }

            } else {
                layout_data.offset_y = base_y - node_outer_height / 2;
                layout_data.offset_x =
                    this.opts.hspace * layout_data.direction +
                    (pd.view.width * (pd.layout.direction + layout_data.direction)) / 2;
                // if (node.parent.isroot) {
                //     layout_data.offset_x += 400; 
                // }
                if (!node.parent.isroot) {
                    layout_data.offset_x += this.opts.pspace * layout_data.direction;
                }
                base_y = base_y - node_outer_height - this.opts.vspace;
            }
            

            total_height += node_outer_height;
        }
        if (nodes_count > 1) {
            total_height += this.opts.vspace * (nodes_count - 1);
        }
        i = nodes_count;
        var middle_height = total_height / 2;
        while (i--) {
            node = nodes[i];
            if (layout_data.direction !== Direction.down) {
                node._data.layout.offset_y += middle_height;
            } 
            // else {
            //     node._data.layout.offset_y += (middle_height / 4);
            // }
        }
        return total_height;
    }

    _layout_offset_top_subnodes(nodes) {
        var total_height = 0;
        var nodes_count = nodes.length;
        var i = nodes_count;
        var node = null;
        var element_node_outer_height = 0;
        var node_outer_height = 0;
        var layout_data = null;
        var el_layout_data = null;
        var base_x = 0;
        var base_y = 0;
        var pd = null; // parent._data
        var el_base_x = 0;
        var el_base_y = 0;

        while (i--) {
            node = nodes[i];
            if (pd == null) {
                pd = node.parent._data;
            }
            if (node.data["option-color"] === "black") {
                el_layout_data = node._data.layout;
                el_layout_data.outer_height = element_node_outer_height;
                element_node_outer_height = Math.max(node._data.view.height, element_node_outer_height);
                if (node.children.length > 1) {
                    element_node_outer_height += this.opts.cousin_space;
                }
                element_node_outer_height = this._layout_offset_top_subnodes(node.children);
            } else {
                layout_data = node._data.layout;
                layout_data.outer_height = node_outer_height;
                node_outer_height = Math.max(node._data.view.height, node_outer_height);
                if (node.children.length > 1) {
                    node_outer_height += this.opts.cousin_space;
                }
                node_outer_height = this._layout_offset_top_subnodes(node.children);
            }

            
            if (!node.expanded) {
                node_outer_height = 0;
                this.set_visible(node.children, false);
            }

            
            if (node.data["option-color"] === "black") {
                el_layout_data.offset_x =
                    this.opts.hspace * el_layout_data.direction +
                    ((pd.view.width) * (pd.layout.direction + el_layout_data.direction )) / 2;
                if (node.parent.isroot) {
                    const function_node = node.parent.children.filter(e=> e.direction === 2 && e.data["option-color"] === "#658ef8");

                    if (function_node.length > 0) {
                    
                        let functionWidth = 0
                        let failureWidth = 0

                        function_node.forEach(e => {
                            if (functionWidth < e._data.view.width) {
                                functionWidth = e._data.view.width
                            }
                        });
                    
                        for (let i = 0; i < function_node.length; i++) {
                            const element = function_node[i];
                            for (let j = 0; j < element.children.length; j++) {
                                if (element.children[j].data["option-color"] === "red") {
                                    if (failureWidth < element.children[j]._data.view.width) {
                                        failureWidth = element.children[j]._data.view.width
                                    }
                                }
                                
                            }
                        }
                        el_layout_data.offset_x += (functionWidth + failureWidth) + this.opts.hspace; 
                    } else {
                        el_layout_data.offset_x += pd.view.width; 
                    }
                    
                    el_layout_data.offset_y = el_base_y - element_node_outer_height / 2;

                    el_base_y = el_base_y + this.opts.vspace * 2;
                    total_height += element_node_outer_height;

                    node.parent.children.sort((a, b) => a.index - b.index);
                
                    const currentIndex = node.parent.children.findIndex(element => element.index === node.index);

                    if (currentIndex !== -1 && currentIndex < node.parent.children.length - 1) {
                        const nextNode = node.parent.children[currentIndex + 1];
                        const nodeFunction = nextNode.children.filter(e=> e.data["option-color"] === "#658ef8");
                        let nodeFailureLength = 0;
                        for (let i = 0; i < nodeFunction.length; i++) {
                                const nodeFailure = nodeFunction[i].children.length;
                                if (nodeFailure) {
                                    nodeFailureLength += nodeFailure
                                }
                        }
                        const nodeElement = nextNode.children.filter(e=> e.data["option-color"] === "black");

                        let level1NodeElNum = nodeElement.length;

                        if (nodeElement && nodeElement.length > 0) {
                            const level2NodeFunction = [] 
                            const level3NodeFunction = []
                            let level2nodeFailureLength = 0;
                            let level3nodeFailureLength = 0;
                            nodeElement.forEach(node => {
                                node.children.forEach(childNode => {
                                    if (childNode.data["option-color"] === "#658ef8") {
                                        level2NodeFunction.push(childNode);
                                        level2nodeFailureLength += childNode.children ? childNode.children.length : 0;
                                    }
                                    if (childNode.children) {
                                        childNode.children.forEach(grandChildNode => {
                                            if (grandChildNode.data["option-color"] === "#658ef8") {
                                                level3NodeFunction.push(grandChildNode);
                                                level3nodeFailureLength += grandChildNode.children ? grandChildNode.children.length : 0;
                                            }
                                        });
                                    }
                                });
                            });
                            let level1NodeFuntionNum = Math.max((nodeFunction.length), (level2NodeFunction.length), (level3NodeFunction.length));
                            let level3NodeElementNum = 0
                            for (let i = 0; i < nodeElement.length; i++) {
                                const element = nodeElement[i].children;
                                if (element && element.length > 1) {
                                    for (let j = 0; j < element.length; j++) {
                                        const elelel = element[j];
                                        if (elelel.data["option-color"] === "black") {
                                            level3NodeElementNum ++
                                        }
                                    }
                                }
                            }

                            if (level3NodeElementNum > 1) {
                                level1NodeElNum += level3NodeElementNum
                            }

                            if (level1NodeElNum > 0) {
                                el_layout_data.offset_y +=  this.opts.vspace * 2 * level1NodeElNum
                                el_base_y +=  this.opts.vspace * 2 * level1NodeElNum
                            }

                            if (level1NodeFuntionNum > 0) {
                                el_layout_data.offset_y += this.opts.vspace * 2 * level1NodeFuntionNum
                                el_base_y +=  this.opts.vspace * 2 * level1NodeFuntionNum
                            }

                            let nodeFailureNum = nodeFailureLength + level2nodeFailureLength + level3nodeFailureLength

                            if (nodeFailureNum > 0) {
                                el_layout_data.offset_y += this.opts.vspace * 2.5 * nodeFailureNum
                                el_base_y +=  this.opts.vspace * 2.5 * nodeFailureNum
                            }

                        }
                    }



                    // const totalNumbers = level1NodeFFNum; 

                    // let counter = 0;

                    // for (let i = 0; i < totalNumbers; i++) {
                    //     counter++;
                    //     if (counter % 2 === 0) {
                    //         el_layout_data.offset_y += pd.view.height / 2 
                    //         el_base_y +=  this.opts.vspace * 3 
                    //     }
                    // }

                    // if (level1NodeFFNum > 0) {
                    //     el_layout_data.offset_y += pd.view.height / 2 * level1NodeFFNum
                    //     el_base_y +=  this.opts.vspace * 3 * level1NodeFFNum
                    // }

                } else {

                    el_layout_data.offset_y = el_base_y - element_node_outer_height / 2;

                    el_layout_data.offset_x += this.opts.pspace * el_layout_data.direction;

                    el_base_y = el_base_y  + this.opts.vspace * 2;
                    total_height += element_node_outer_height;


                    // X軸
                    let function1Width = 0
                    let failure1Width = 0
                    let element2Width = 0
                    let function2Width = 0
                    let failure2Width = 0

                    if (node.parent.parent.isroot) {
                        // console.log('level2');
                        // console.log('node.topic', node.topic);
                        const funtion_node = node.parent.children.filter(e=> e.data["option-color"] === "#658ef8");
                        funtion_node.forEach(e => {
                            if (function1Width < e._data.view.width) {
                                function1Width = e._data.view.width
                            }
                        });
                    
                        for (let i = 0; i < funtion_node.length; i++) {
                            const element = funtion_node[i];
                            for (let j = 0; j < element.children.length; j++) {
                                if (element.children[j].data["option-color"] === "red") {
                                    if (failure1Width < element.children[j]._data.view.width) {
                                        failure1Width = element.children[j]._data.view.width
                                    }
                                }
                                
                            }
                        }
                        el_layout_data.offset_x += function1Width + function1Width

                    } else if (node.parent.parent.parent.isroot) {
                        // console.log('level3');
                        const nodeElement = node.parent.parent.children.filter(e=> e.data["option-color"] === "black");

                        if (nodeElement && nodeElement.length > 0) {
                            nodeElement.forEach(e=> {
                                // console.log(e.topic);
                                // console.log(e._data.view.width);
                                if (element2Width < e._data.view.width) {
                                    element2Width = e._data.view.width
                                   
                                }
                            })
                        }

                        const nodeFunction = [];

                        for (let i = 0; i < nodeElement.length; i++) {
                            for (let j = 0; j < nodeElement[i].children.length; j++) {
                                if (nodeElement[i].children[j].data["option-color"] === "#658ef8") {
                                    if (function2Width < nodeElement[i].children[j]._data.view.width) {
                                        function2Width = nodeElement[i].children[j]._data.view.width
                                    }
                                    nodeFunction.push(nodeElement[i].children[j]);
                                }
                                
                            }
                            
                        }

                        for (let k = 0; k < nodeFunction.length; k++) {
                            for (let l = 0; l < nodeFunction[k].children.length; l++) {
                                if (nodeFunction[k].children[l].data["option-color"] === "red") {
                                    if (failure2Width < nodeFunction[k].children[l]._data.view.width) {
                                        failure2Width = nodeFunction[k].children[l]._data.view.width
                                    }
                                }
                                
                            }
                            
                        }

                        el_layout_data.offset_x += element2Width + function2Width + failure2Width

                    } 


                    const allNodes = node.parent.children.sort((a, b) => a.index - b.index);

                    const currentIndexIndex = node.parent.children.findIndex(element => element.index === node.index);

                    if (currentIndexIndex !== -1 && currentIndexIndex < allNodes.length - 1) {
                        
                        const nextNode = allNodes[currentIndexIndex + 1];
                        // const nodeFunction = nextNode.children.filter(e=> e.data["option-color"] === "#658ef8");
                        
                        // let nodelevel2FunctionNum = 0;
                        // let nodelevel3FunctionNum = 0;

                        // let nodelevel2FailureNum = 0;
                        // let nodelevel3FailureNum = 0;

                        // nodelevel2FunctionNum = nodeFunction.length;
                       
                        const nodelevel3Element = nextNode.children.filter(e=> e.data["option-color"] === "black");

                        if (nodelevel3Element.length > 1) {
                            el_layout_data.offset_y +=  this.opts.vspace * 2 * nodelevel3Element.length
                            el_base_y +=  this.opts.vspace * 2 * nodelevel3Element.length
                        }

                        // if (nodelevel3Element && nodelevel3Element.length > 0) {
                        //     nodelevel3Element.forEach(e=> {
                        //        e.children.forEach(ee=> {
                        //             if (ee.data["option-color"] === "#658ef8") {
                        //                 nodelevel3FunctionNum++
                        //             }
                        //             if (ee.children.length > 0) {
                        //                 nodelevel3FailureNum += ee.children.length
                        //             }
                        //        })
                        //     })
                        // }

                        // let nodeFuctionNumSum = nodelevel2FunctionNum + nodelevel3FunctionNum

                        // let nodeFailureNumSum = nodelevel2FailureNum + nodelevel3FailureNum

                        // if (nodeFuctionNumSum > 0) {
                        //     el_layout_data.offset_y += this.opts.vspace * 2 * nodeFuctionNumSum
                        //     el_base_y +=  this.opts.vspace * 2 * nodeFuctionNumSum
                        // }
                        
                        // if (nodeFailureNumSum > 0) {
                        //     el_layout_data.offset_y += this.opts.vspace * 2.5 * nodeFailureNumSum
                        //     el_base_y +=  this.opts.vspace * 2.5 * nodeFailureNumSum
                        // }

                        if (node.parent.parent.isroot) {
                            const allNodes = node.parent.children.sort((a, b) => a.index - b.index);

                            const level2Element = allNodes.filter(e=> e.data["option-color"] === "black");

                            const currentIndexIndex = node.parent.children.findIndex(element => element.index === node.index);
                            if (currentIndexIndex !== -1 && currentIndexIndex < level2Element.length - 1) {

                                let level2FunctionNum = 0;
                                let level2FailureNum = 0;

                                const nextNode = level2Element[currentIndexIndex + 1];
                                if (nextNode) {
                                    level2FunctionNum = nextNode.children.length
                                    
                                    if (nextNode.children && nextNode.children.length > 0) {
                                        nextNode.children.forEach(e=>{
                                            level2FailureNum += e.children.length
                                        })
                                    }
                                    if (level2FunctionNum > 0) {
                                        el_layout_data.offset_y += this.opts.vspace * 2 * level2FunctionNum
                                        el_base_y +=  this.opts.vspace * 2 * level2FunctionNum
                                    }

                                    if (level2FailureNum > 0) {
                                        el_layout_data.offset_y += this.opts.vspace * 2.5 * level2FailureNum
                                        el_base_y +=  this.opts.vspace * 2.5 * level2FailureNum
                                    }
                                }
                            }
                        } else {
                           
                            const allNodes = node.parent.children.sort((a, b) => a.index - b.index);

                            const level3Element = allNodes.filter(e=> e.data["option-color"] === "black");

                            const currentIndexIndex = node.parent.children.findIndex(element => element.index === node.index);

                            if (currentIndexIndex !== -1 && currentIndexIndex < level3Element.length - 1) {
                                // console.log('level3');
                                // console.log('node.topic', node.topic);

                                let level3FunctionNum = 0;
                                let level3FailureNum = 0;

                                const nextNode = allNodes[currentIndexIndex + 1];
                                if (nextNode) {
                                    level3FunctionNum = nextNode.children.length
                                    
                                    if (nextNode.children && nextNode.children.length > 0) {
                                        nextNode.children.forEach(e=>{
                                            level3FailureNum += e.children.length
                                        })
                                    }
                                    if (level3FunctionNum > 0) {
                                        el_layout_data.offset_y += this.opts.vspace * 2 * level3FunctionNum
                                        el_base_y +=  this.opts.vspace * 2 * level3FunctionNum
                                    }

                                    if (level3FailureNum > 0) {
                                        el_layout_data.offset_y += this.opts.vspace * 2.5 * level3FailureNum
                                        el_base_y +=  this.opts.vspace * 2.5 * level3FailureNum
                                    }
                                }
                            }


                        }

                        
                    }
                }
            } else{
                layout_data.offset_y = base_y + pd.view.height / 2 ;
                layout_data.offset_x = base_x + (pd.view.width * 1.2);
                base_y += node_outer_height + this.opts.vspace * 3;
                const allNodes = node.parent.children.sort((a, b) => a.index - b.index)

                const nodeFF = allNodes.filter(e=> e.data["option-color"] !== "black");

                const currentIndexIndex = nodeFF.findIndex(element => element.index === node.index);

                let level2ElementWidth = 0

                let level3ElementWidth = 0

                if (node.data["option-color"] === "#658ef8") {
                    if (node.parent.parent.parent) {
                        if (node.parent.parent.parent.isroot) {
                            // console.log('level2');
                            // console.log('node.topic', node.topic);
                            const nodeElement = node.parent.parent.children.filter(e=> e.data["option-color"] === "black");
                            if (nodeElement && nodeElement.length > 0) {
                                nodeElement.forEach(e=> {
                                    if (level2ElementWidth < e._data.view.width) {
                                        level2ElementWidth = e._data.view.width
                                    }
                                })
                            }
    
                            if (level2ElementWidth > node.parent._data.view.width) {
                                layout_data.offset_x += ((level2ElementWidth - node.parent._data.view.width)*1.2)
                            }
                        } else if (node.parent.parent.parent.parent.isroot) {
                            // console.log('level3');
                            // console.log('node.topic', node.topic);
                            const nodeElement = node.parent.parent.children.filter(e=> e.data["option-color"] === "black");
                            if (nodeElement && nodeElement.length > 0) {
                                nodeElement.forEach(e=> {
                                    if (level3ElementWidth < e._data.view.width) {
                                        level3ElementWidth = e._data.view.width
                                    }
                                })
                            }
                            if (level3ElementWidth > node.parent._data.view.width) {
                                layout_data.offset_x += ((level3ElementWidth - node.parent._data.view.width)*1.2)
                            }
                        } 
                    }  
                }

                let level1FuntionWidth = 0

                let level2FuntionWidth = 0

                let level3FuntionWidth = 0

                if (node.data["option-color"] === "red") {
                    if (node.parent.parent.parent.isroot) {
                        // console.log('level1');
                        // console.log('node.topic', node.topic);
                        const nodeFunction = node.parent.parent.children.filter(e=> e.data["option-color"] === "#658ef8");
                        if (nodeFunction && nodeFunction.length > 0) {
                            nodeFunction.forEach(e=> {
                                if (level1FuntionWidth < e._data.view.width) {
                                    level1FuntionWidth = e._data.view.width
                                }
                            })
                        }
                        if (level1FuntionWidth > node.parent._data.view.width) {
                            layout_data.offset_x += ((level1FuntionWidth - node.parent._data.view.width)*1.2)
                        }
                    } else if (node.parent.parent.parent.parent.isroot) {
                        // console.log('level2');
                        // console.log('node.topic', node.topic);
                        const nodeFunction = node.parent.parent.parent.children.filter(e=> e.data["option-color"] === "#658ef8");
                        if (nodeFunction && nodeFunction.length > 0) {
                            nodeFunction.forEach(e=> {
                                if (level2FuntionWidth < e._data.view.width) {
                                    level2FuntionWidth = e._data.view.width
                                }
                            })
                        }

                        if (level2FuntionWidth > node.parent._data.view.width) {
                            layout_data.offset_x += ((level2FuntionWidth - node.parent._data.view.width)*1.2)
                        }
                    } else {
                        // console.log('level3');
                        // console.log('node.topic', node.topic);
                        const nodeFunction = node.parent.parent.parent.children.filter(e=> e.data["option-color"] === "#658ef8");
                        if (nodeFunction && nodeFunction.length > 0) {
                            nodeFunction.forEach(e=> {
                                if (level3FuntionWidth < e._data.view.width) {
                                    level3FuntionWidth = e._data.view.width
                                }
                            })
                        }
                        if (level3FuntionWidth > node.parent._data.view.width) {
                            layout_data.offset_x += ((level3FuntionWidth - node.parent._data.view.width)*1.2)
                        }
                    }   
                }

                let nodeFailureNumSum = 0;

                if (currentIndexIndex !== -1 && currentIndexIndex < nodeFF.length - 1) {

                    const nextNode = nodeFF[currentIndexIndex + 1];

                    const nodelevel3Failure = nextNode.children.filter(e=> e.data["option-color"] === "red");
                    nodeFailureNumSum += nodelevel3Failure.length;
                }



                if (nodeFailureNumSum > 0) {
                    layout_data.offset_y += this.opts.vspace * 2.5 * nodeFailureNumSum
                    base_y +=  this.opts.vspace * 2.5 * nodeFailureNumSum
                }

                total_height += node_outer_height 
            }
        }
        // if (nodes_count > 1) {
        //     total_height += this.opts.vspace  * (nodes_count - 1);
        // }
        i = nodes_count;
        var middle_height = total_height / 2;
        while (i--) {
            node = nodes[i];
            node._data.layout.offset_y += middle_height;   
        }
        return total_height;
    }

    _layout_offset_right_subnodes(nodes) {
        var total_height = 0;
        var nodes_count = nodes.length;
        var i = nodes_count;
        var node = null;
        var node_outer_height = 0;
        var layout_data = null;
        var base_x = 0;
        var base_y = 0;
        var pd = null; // parent._data
        while (i--) {
            node = nodes[i];
            layout_data = node._data.layout;
            if (pd == null) {
                pd = node.parent._data;
            }
    
            node_outer_height = this._layout_offset_right_subnodes(node.children);
            if (!node.expanded) {
                node_outer_height = 0;
                this.set_visible(node.children, false);
            }
            node_outer_height = Math.max(node._data.view.height, node_outer_height);
            if (node.children.length > 1) {
                node_outer_height += this.opts.cousin_space;
            }

            layout_data.outer_height = node_outer_height;

            layout_data.offset_y = base_y - node_outer_height / 2;
            layout_data.offset_x =
                this.opts.hspace * layout_data.direction +
                (pd.view.width * (pd.layout.direction + layout_data.direction)) / 2;
            if (!node.parent.isroot) {
                layout_data.offset_x += this.opts.pspace * layout_data.direction;
            }
            base_y = base_y - node_outer_height - this.opts.vspace;
            

            total_height += node_outer_height;
        }
        if (nodes_count > 1) {
            total_height += this.opts.vspace * (nodes_count - 1);
        }
        i = nodes_count;
        var middle_height = total_height / 2;
        while (i--) {
            node = nodes[i];
            if (layout_data.direction !== Direction.down) {
                node._data.layout.offset_y += middle_height;
            } 
            // else {
            //     node._data.layout.offset_y += (middle_height / 4);
            // }
        }
        return total_height;
    }

    // layout the y axis only, for collapse/expand a node
    _layout_offset_subnodes_height(nodes) {
        var total_height = 0;
        var nodes_count = nodes.length;
        var i = nodes_count;
        var node = null;
        var node_outer_height = 0;
        var layout_data = null;
        var base_y = 0;
        var pd = null; // parent._data
        while (i--) {
            node = nodes[i];
            layout_data = node._data.layout;
            if (pd == null) {
                pd = node.parent._data;
            }

            node_outer_height = this._layout_offset_subnodes_height(node.children);
            if (!node.expanded) {
                node_outer_height = 0;
            }
            node_outer_height = Math.max(node._data.view.height, node_outer_height);
            if (node.children.length > 1) {
                node_outer_height += this.opts.cousin_space;
            }

            layout_data.outer_height = node_outer_height;
            layout_data.offset_y = base_y - node_outer_height / 2;
            base_y = base_y - node_outer_height - this.opts.vspace;
            total_height += node_outer_height;
        }
        if (nodes_count > 1) {
            total_height += this.opts.vspace * (nodes_count - 1);
        }
        i = nodes_count;
        var middle_height = total_height / 2;
        while (i--) {
            node = nodes[i];
            node._data.layout.offset_y += middle_height;
        }
        return total_height;
    }
    get_node_offset(node) {
        var layout_data = node._data.layout;
        var offset_cache = null;
        if ('_offset_' in layout_data && this.cache_valid) {
            offset_cache = layout_data._offset_;
        } else {
            offset_cache = { x: -1, y: -1 };
            layout_data._offset_ = offset_cache;
        }
        if (offset_cache.x == -1 || offset_cache.y == -1) {
            var x = layout_data.offset_x;
            var y = layout_data.offset_y;
            if (!node.isroot) {
                var offset_p = this.get_node_offset(node.parent);
                x += offset_p.x;
                y += offset_p.y;
            }
            offset_cache.x = x;
            offset_cache.y = y;
        }
        return offset_cache;
    }
    get_node_point(node) {
        var view_data = node._data.view;
        var offset_p = this.get_node_offset(node);
        var p = {};
        node.p = p;
        if (node.direction === 2) {
            p.x = offset_p.x + 70 / 2;
            p.y = offset_p.y - view_data.height / 2;
        } else {
            p.x = offset_p.x + (view_data.width * (node._data.layout.direction - 1)) / 2;
            p.y = offset_p.y - view_data.height / 2;
        }
        return p;
    }
    get_node_point_in(node) {
        var p = this.get_node_offset(node);
        return p;
    }
    get_node_point_out(node) {
        var layout_data = node._data.layout;
        var pout_cache = null;
        if ('_pout_' in layout_data && this.cache_valid) {
            pout_cache = layout_data._pout_;
        } else {
            pout_cache = { x: -1, y: -1 };
            layout_data._pout_ = pout_cache;
        }
        if (pout_cache.x == -1 || pout_cache.y == -1) {
            if (node.isroot) {
                pout_cache.x = 0;
                pout_cache.y = 0;
            } else {
                var view_data = node._data.view;
                var offset_p = this.get_node_offset(node);
                pout_cache.x =
                    offset_p.x + (view_data.width + this.opts.pspace) * node._data.layout.direction;
                pout_cache.y = offset_p.y;
            }
        }
        return pout_cache;
    }
    get_expander_point(node) {
        var p = this.get_node_point_out(node);
        var ex_p = {};
        ex_p.y = p.y - Math.ceil(this.opts.pspace / 2);
        if (node._data.layout.direction == Direction.right) {
            ex_p.x = p.x - this.opts.pspace;
        } else if (node._data.layout.direction == Direction.top) {
            if (node.data["option-color"] === "black") {
                ex_p.x = p.x - 20
                if (node.parent.isroot) {
                    ex_p.y -= 10
                }
            };
            if (node.data["option-color"] === "#658ef8") {
                ex_p.x = p.x - 35
            }
        } else {
            ex_p.x = p.x - 410
            ex_p.y -= 10
        }
        return ex_p;
    }
    get_min_size() {
        var nodes = this.jm.mind.nodes;
        var node = null;
        var pout = null;
        for (var node_id in nodes) {
            node = nodes[node_id];
            pout = this.get_node_point_out(node);
            if (pout.x > this.bounds.e) {
                this.bounds.e = pout.x;
            }
            if (pout.x < this.bounds.w) {
                this.bounds.w = pout.x;
            }
        }
        return {
            w: this.bounds.e - this.bounds.w,
            h: this.bounds.s - this.bounds.n,
        };
    }
    toggle_node(node) {
        if (node.isroot) {
            return;
        }
        if (node.expanded) {
            this.collapse_node(node);
        } else {
            this.expand_node(node);
        }
    }
    expand_node(node) {
        node.expanded = true;
        this.part_layout(node);
        this.set_visible(node.children, true);
        this.jm.invoke_event_handle(EventType.show, {
            evt: 'expand_node',
            data: [],
            node: node.id,
        });
    }
    collapse_node(node) {
        node.expanded = false;
        this.part_layout(node);
        this.set_visible(node.children, false);
        this.jm.invoke_event_handle(EventType.show, {
            evt: 'collapse_node',
            data: [],
            node: node.id,
        });
    }
    expand_all() {
        var nodes = this.jm.mind.nodes;
        var c = 0;
        var node;
        for (var node_id in nodes) {
            node = nodes[node_id];
            if (!node.expanded) {
                node.expanded = true;
                c++;
            }
        }
        if (c > 0) {
            var root = this.jm.mind.root;
            this.part_layout(root);
            this.set_visible(root.children, true);
        }
    }
    collapse_all() {
        var nodes = this.jm.mind.nodes;
        var c = 0;
        var node;
        for (var node_id in nodes) {
            node = nodes[node_id];
            if (node.expanded && !node.isroot) {
                node.expanded = false;
                c++;
            }
        }
        if (c > 0) {
            var root = this.jm.mind.root;
            this.part_layout(root);
            this.set_visible(root.children, true);
        }
    }
    expand_to_depth(target_depth, curr_nodes, curr_depth) {
        if (target_depth < 1) {
            return;
        }
        var nodes = curr_nodes || this.jm.mind.root.children;
        var depth = curr_depth || 1;
        var i = nodes.length;
        var node = null;
        while (i--) {
            node = nodes[i];
            if (depth < target_depth) {
                if (!node.expanded) {
                    this.expand_node(node);
                }
                this.expand_to_depth(target_depth, node.children, depth + 1);
            }
            if (depth == target_depth) {
                if (node.expanded) {
                    this.collapse_node(node);
                }
            }
        }
    }
    part_layout(node) {
        var root = this.jm.mind.root;
        if (!!root) {
            var root_layout_data = root._data.layout;
            if (node.isroot) {
                root_layout_data.outer_height_right = this._layout_offset_subnodes_height(
                    root_layout_data.right_nodes
                );
                root_layout_data.outer_height_left = this._layout_offset_subnodes_height(
                    root_layout_data.left_nodes
                );
            } else {
                if (node._data.layout.direction == Direction.right) {
                    root_layout_data.outer_height_right = this._layout_offset_subnodes_height(
                        root_layout_data.right_nodes
                    );
                } else {
                    root_layout_data.outer_height_left = this._layout_offset_subnodes_height(
                        root_layout_data.left_nodes
                    );
                }
            }
            this.bounds.s = Math.max(
                root_layout_data.outer_height_left,
                root_layout_data.outer_height_right
            );
            this.cache_valid = false;
        } else {
            logger.warn('can not found root node');
        }
    }
    set_visible(nodes, visible) {
        var i = nodes.length;
        var node = null;
        var layout_data = null;
        while (i--) {
            node = nodes[i];
            layout_data = node._data.layout;

            if (node.expanded) {
                this.set_visible(node.children, visible);
            } else {
                this.set_visible(node.children, false);
            }
            if (!node.isroot) {
                node._data.layout.visible = visible;
            }

            if (node.parent.parent) {
                if (node.parent.parent.isroot) {
                    if (node.data["option-color"] === "#658ef8") {
                        node._data.layout.visible = true;
                        // this.set_visible(node.children, true);
                    }
                }
            }

            if (node.parent.parent.parent) {
                if (node.parent.parent.parent.isroot) {
                    if (node.data["option-color"] === "red") {
                        node._data.layout.visible = true;
                        // this.set_visible(node.children, true);
                    }
                }
            }

        }
    }
    is_expand(node) {
        return node.expanded;
    }
    is_visible(node) {
        var layout_data = node._data.layout;
        if ('visible' in layout_data && !layout_data.visible) {
            return false;
        } else {
            return true;
        }
    }
}

